import { 
    MOVIE_CONTAINER_CLASS, 
    MOVIE_CHECKBOX_CLASS,
    MOVIE_TITLE_CLASS,
    MOVIE_DELETE_BTN_CLASS,
    MOVIE_DONE_CLASS,
    MOVIE_CHECKBOX_DONE_CLASS
} from "../constants.js"

import deleteBtnImage from "../assets/deleteBtn.png";

export class moviesView {
    constructor({ onAddMovie, onDeleteMovie, onUpdateMovie }){
        this.onAddMovie = onAddMovie;
        this.onDeleteMovie = onDeleteMovie;
        this.onUpdateMovie = onUpdateMovie;
        
        this.containerNode = document.querySelector('.movies__Container');

        this.inputTitleNode = document.querySelector('.js-input__title');
        this.inputTitleNode.addEventListener('change',() => this._changeTitleHandler());

        this.addMovieBtnNode = document.querySelector('.js-input__btn');
        this.addMovieBtnNode.addEventListener('click', () => this._addMovieHandler());
    }

    init = () => {
        this._clear();
        this._changeTitleHandler();
    }

    _addMovieHandler = () => {
        const title = this.inputTitleNode.value;
        this.inputTitleNode.value = '';

        this.onAddMovie(title);
    }

    _clear = () => {
        this.containerNode.innerHTML = '';
    }

    render = (movies) => {
        movies.forEach(movie => {

            const movieContainerNode = document.createElement('div');
            movieContainerNode.classList.add(MOVIE_CONTAINER_CLASS);
            movieContainerNode.setAttribute('id', movie.id);

            const checkboxBtnNode = document.createElement('button');
            checkboxBtnNode.classList.add(MOVIE_CHECKBOX_CLASS);
            checkboxBtnNode.addEventListener('click', (e) => {
                
                e.target.parentNode.classList.toggle(MOVIE_DONE_CLASS);
                e.target.classList.toggle(MOVIE_CHECKBOX_DONE_CLASS);

                const id = e.target.parentNode.getAttribute('id');
                const done = e.target.parentNode.classList.contains(MOVIE_DONE_CLASS);

                this.onUpdateMovie(id, done);
            });

            if(movie.done){
                movieContainerNode.classList.add(MOVIE_DONE_CLASS);
                checkboxBtnNode.classList.add(MOVIE_CHECKBOX_DONE_CLASS);
            }

            movieContainerNode.append(checkboxBtnNode);

            const titleNode = document.createElement('p');
            titleNode.innerText = movie.title;
            titleNode.classList.add(MOVIE_TITLE_CLASS);

            movieContainerNode.append(titleNode);

            const deleteBtnNode = document.createElement('button');
            deleteBtnNode.classList.add(MOVIE_DELETE_BTN_CLASS);
            deleteBtnNode.addEventListener('click',(e) => {
                const parentNode = e.target.parentNode.parentNode;
                const id = parentNode.getAttribute('id');
                parentNode.remove();

                this.onDeleteMovie(id);
            })

            const deleteBtnImageNode = document.createElement('img');
            deleteBtnImageNode.src = deleteBtnImage;
            

            deleteBtnNode.append(deleteBtnImageNode);
            movieContainerNode.append(deleteBtnNode);
            
            this.containerNode.append(movieContainerNode);
        });
    }

    _changeTitleHandler = () => {
        if(this.inputTitleNode.value.length === 0){
            this.addMovieBtnNode.setAttribute('disabled', '');
        } else {
            this.addMovieBtnNode.removeAttribute('disabled');
        }
    }
}
