import { moviesModel } from "./moviesModel.js";
import { moviesView } from "./moviesView.js";
import { firebaseApi } from "../firebaseApi.js";

export class moviesController {
    constructor() {
        this.view = new moviesView({
            onAddMovie: this._movieAddHandler,
            onDeleteMovie: this._movieDeleteHandler,
            onUpdateMovie: this._updateMovieHandler
        });
        this.model = new moviesModel();
        this.firebaseApi = new firebaseApi();
    }

    init() {
        this.view.init();

        this.firebaseApi.getMoviesData().then((result) => {
            let movies = [];
        
            result.forEach((doc) => {
                const movie = {
                    id: doc.id,
                    title: doc.data().title,
                    done: doc.data().done
                };
        
                movies.push(movie);
            });
        
            this.model.init(movies);
            this.view.render(this.model.getMovies());
        })
    }

    _updateMovieHandler = (id, done) => {
        if(!id) return;

        const movie = this.model.getMovie(id);
        movie.done = done;

        this.model.updateMovie(movie);
        this.firebaseApi.updateMovie(movie);
    }

    _movieAddHandler = (title) => {
        if(!title) return;
        
        const movie = this.model.addMovie(title);
        this.firebaseApi.addMovieData(movie);
        this.view.render([movie]);
    }

    _movieDeleteHandler = (id) => {
        const movie = this.model.deleteMovie(id);
        this.firebaseApi.deleteMovies([movie]);
    }
}