import { initializeApp } from "firebase/app";
import { getFirestore,
         collection,
         getDocs, 
         writeBatch, 
         doc,
         setDoc} from "firebase/firestore";

export class firebaseApi {
    constructor(){
        this.MOVIE_DB_NAME = 'movies';
        this.firebaseConfig = {
            apiKey: "AIzaSyCX6T70LUL9wWU51heFSAC37rHQWu_8cO4",
            authDomain: "moviesearch-2e460.firebaseapp.com",
            projectId: "moviesearch-2e460",
            storageBucket: "moviesearch-2e460.appspot.com",
            messagingSenderId: "532563958464",
            appId: "1:532563958464:web:69fba86f1dfb66b3cc6b25"
          };
          this.app = initializeApp(this.firebaseConfig);
          this.db = getFirestore(this.app);
    }

    addMovieData = (movieData) => {
        return setDoc(doc(this.db, this.MOVIE_DB_NAME, movieData.id), movieData);
    }

    getMoviesData = () => {
        return getDocs(collection(this.db, this.MOVIE_DB_NAME));
    }

    deleteMovies = (movies) => {
        const batch = writeBatch(this.db);
    
        movies.forEach((movie) => {
            const ref = doc(this.db, this.MOVIE_DB_NAME, movie.id);
            batch.delete(ref);
        });
    
        return batch.commit();
    }

    updateMovie = (movie) => {
        setDoc(doc(this.db, this.MOVIE_DB_NAME, movie.id), movie);
    }
}