import {v4 as uuidv4 } from "uuid";

export class moviesModel {
    constructor() {
        this.moviesID = [];
        this.movies = new Map();
    }

    init = (movies) => {
        movies.forEach(movie => {
            this.moviesID.push(movie.id);
            this.movies.set(movie.id, movie);
        });
    }

    addMovie = (title) => {
        const movie = {
            id: uuidv4(),
            title: title,
            done: false
        };

        this.movies.set(movie.id, movie);
        this.moviesID.push(movie.id);

        return movie;
    }

    updateMovie = (movie) => {
        this.movies.set(movie.id, movie);

        return movie;
    }

    deleteMovie = (id) => {
        const movie = this.movies.get(id);
        
        this.movies.delete(id);
        this.moviesID.splice(this.moviesID.indexOf(id),1);

        return movie;
    }

    getMovie(id) {
        return this.movies.get(id);
    }

    getMovies() {
        return Array.from(this.movies.values());
    }
}